
.el-header{
	position: fixed;
	top: 0;
	left: 0;
	z-index: 9999;
	width: 100%;
	background: #409EFF;
}
.cg-login{
	float: left;
	padding: 5px 0;
}
.cg-login img{
	width: 200px;
	height: 50px;
	display: inline-block;
}
.cg-menu{
	display: inline-block;
	background: #409EFF;
}

.el-main{
	margin: 75px auto 0;
}
.el-main>.w1200{
	margin: 0 auto;
}
.wm-router-card:hover{
	text-decoration: none;
}
h5{
	text-decoration: none;
	color: #409EFF;
}
.desc {
	display: inline-block;
	line-height: 18px;
	height: 36px;
	overflow: hidden;
    font-size: 12px;
    color: #999;
    margin-bottom: 0;
}
.image {
    width: 100%;
    display: block;
}
